
.header-wrapper .nav-wrapper .navigator{
  width: 100%;
  height: 100%;
  background:#FFF;
}
.header-wrapper .navigator .left-nav {
  width: 100%;
  height: 100%;
}
.header-wrapper .navigator .right-nav{
  width: 100%;
  height: 100%;
}
.header-wrapper .nav-wrapper .navigator .ant-menu-item{
  height: 60px;
  color: #62A5E8;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
}
.header-wrapper .nav-wrapper .navigator .ant-menu-item:hover ,.header-wrapper .nav-wrapper .navigator .ant-menu-item-selected {
  border-bottom: 2px solid #62A5E8 ;
}
.header-wrapper .search-bar{
  margin-top: 20px;
}